import React from "react";
import { MDBCard, MDBView, MDBMask } from "mdbreact";

class FoliaCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = { dados: {} };
  }
  componentDidMount() {
    this.setState({ dados: this.props.dados });
  }
  render() {
    return (
      <MDBCard>
        <MDBView>
          <img
            src={this.state.dados.foto}
            className="img-fluid"
            alt={this.state.dados.nome}
          />
          <MDBMask overlay="teal-slight">
            <div className="d-flex flex-row">
              <div className="d-flex align-items-start flex-column flex-fill">
                <div className="mb-auto white-text text-left">
                  {this.state.dados.censura}
                </div>
                <div className="white-text">{this.state.dados.nome}</div>
                <div className="white-text">{this.state.dados.preco}</div>
              </div>
              <div className="d-flex align-items-end flex-column flex-fill">
                <div className="mb-auto white-text text-right">
                  {this.state.dados.modalidade}
                </div>
              </div>
            </div>
          </MDBMask>
        </MDBView>
      </MDBCard>
    );
  }
}

export default FoliaCard;
