import React from "react";
import FoliaCarousel from "../common/FoliaCarousel";
import BarraServicos from "../common/BarraServicos";

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = { camarotes: [], blocos: [], navios: [], hoteis: [] };
  }

  componentDidMount() {
    fetch("./data/db.json")
      .then(response => response.json())
      .then(produtos => {
        this.setState({
          camarotes: produtos[0].camarotes,
          blocos: produtos[1].blocos
        });
      })
      .catch(function(error) {
        // handle error
        console.log(error);
      });
  }

  render() {
    return (
      <div className="mb-5">
        <h1 className="text-white mb-4">HOME</h1>
        <BarraServicos />
        <FoliaCarousel titulo="CAMAROTES" dados={this.state.camarotes} />
        <FoliaCarousel
          titulo="BLOCOS"
          dados={this.state.blocos}
          className="mt-4"
        />
        <h1 className="text-white mb-4">HOME</h1>
      </div>
    );
  }
}

export default Home;
